export const __metadata = { PACKAGE_NAME: '@wix/blog' };

type BlogCacheNonNullablePaths =
  | `siteSupportedLanguages`
  | `siteSupportedLanguages.${number}.languageCode`
  | `siteSupportedLanguages.${number}.status`
  | `siteSupportedLanguages.${number}.flag`
  | `siteSupportedLanguages.${number}.resolutionMethod`
  | `pagesData.blogPage.title`
  | `pagesData.blogPage.pageId`
  | `pagesData.blogPage.hidden`
  | `pagesData.blogPage.path`
  | `pagesData.blogPage.url`
  | `pagesData.blogPage.isSmProtected`
  | `pagesData.blogPage.isDynamic`
  | `pagesData.blogPage.platformApps`;

export function getBlogCache(httpClient: HttpClient): GetBlogCacheSignature {
  return () =>
    universalGetBlogCache(
      // @ts-ignore
      { httpClient }
    );
}

interface GetBlogCacheSignature {
  /**
   * Gets blog cache.
   */
  (): Promise<
    NonNullablePaths<
      GetBlogCacheResponse,
      {
        [P in BlogCacheNonNullablePaths]: `blogCache.${P}`;
      }[BlogCacheNonNullablePaths]
    >
  >;
}

