/** BlogCache is the main entity of BlogCacheService */
export interface BlogCache {
  /**
   * MetaSite ID.
   * @format GUID
   * @readonly
   */
  _id?: string | null;
  /** Site supported languages. */
  siteSupportedLanguages?: SiteSupportedLanguage[];
  /** Pages data. */
  pagesData?: PagesData;
  /** Site properties. */
  siteProperties?: SiteProperties;
  /**
   * Date the blog cache was last updated
   * @readonly
   */
  _updatedDate?: Date | null;
}

export interface SiteSupportedLanguage {
  /**
   * Language code
   * @format LANGUAGE
   */
  languageCode?: string;
  /** Locale status. */
  status?: LocaleStatusWithLiterals;
  /** Flag code. */
  flag?: FlagWithLiterals;
  /** Resolution method. */
  resolutionMethod?: ResolutionMethodWithLiterals;
}

export enum LocaleStatus {
  /** Value not specified */
  UNDEFINED_STATUS = 'UNDEFINED_STATUS',
  /** Locale was added to site but was not activated */
  INACTIVE = 'INACTIVE',
  /** Locale was added to site and activated */
  ACTIVE = 'ACTIVE',
  /** Default locale (always active) */
  DEFAULT = 'DEFAULT',
}

/** @enumType */
export type LocaleStatusWithLiterals =
  | LocaleStatus
  | 'UNDEFINED_STATUS'
  | 'INACTIVE'
  | 'ACTIVE'
  | 'DEFAULT';

